@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:ital,wght@0,400;0,500;0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --primary-blue: #25c5db;
  --primary-off-white: #e4f0f1;
  --primary-red: #640f05;
}

.App{
  background: #25c5db;
  background: var(--primary-blue);
  min-height: 100vh;
  overflow-x: hidden;
}

.boxwidth {
  width: 80%;
  min-width: 90vw;
  margin: 0 auto;
}

.clickToExit{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

.logo{
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
}
.Navigation{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: var(--primary-blue);
  border-radius: 0 0 20px 20px;
}

.fixed_Nav_home{
  z-index: 1;
  -webkit-animation: Navdrop 500ms ease-in;
          animation: Navdrop 500ms ease-in;
}

@-webkit-keyframes Navdrop{
  from{
    -webkit-transform: translateY(-10em);
            transform: translateY(-10em);
  } to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes Navdrop{
  from{
    -webkit-transform: translateY(-10em);
            transform: translateY(-10em);
  } to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.Navigation nav{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 5em;
}

.Navigation .nav_home{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.Navigation .logo_home{
  position: absolute;
  top: 10em;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.Navigation .logo{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
  font-family: "Red Hat Text", sans-serif;
  color: #fff;
}

.Navigation .logo h1 {
  font-style: italic;
  font-weight: bold;
  text-transform: uppercase;
}

.Navigation .logo_home h1{
  font-size: 18vw;
  -webkit-transform: translateX(-4vw);
          transform: translateX(-4vw);
}

.Navigation .logo h3{
  font-size: 1.2em;
  margin-left: 0.5em;
}

.Navigation .right .hamburger{
  cursor: pointer;
}

.Navigation .right .hamburger_menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style: none;
  text-decoration: none;
  position: absolute;
  top: 4.3em;
  right: 5%;
  padding: 1em 0.5em;
  /* background: #fff; */
  border-radius: 5px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 500ms ease;
  transition: transform 500ms ease;
  transition: transform 500ms ease, -webkit-transform 500ms ease;
  
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
}

.Navigation .right .menu_open{
  -webkit-animation: fadeIn 500ms ease;
          animation: fadeIn 500ms ease;
}

.Navigation .right .menu_close{
  -webkit-transform: translateX(15em);
          transform: translateX(15em);
  -webkit-animation: fadeOut 500ms ease;
          animation: fadeOut 500ms ease;
}

@-webkit-keyframes fadeIn{
  0% {
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translateX(10em);
            transform: translateX(10em);
    opacity: 0;
  } 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeIn{
  0% {
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translateX(10em);
            transform: translateX(10em);
    opacity: 0;
  } 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut{
  0% {
    /* transform: translateX(0); */
    opacity: 1;
  } 75% {
    /* transform: translateX(10em); */
    opacity: 0;
  } 100% {
    display: none;
  }
}

@keyframes fadeOut{
  0% {
    /* transform: translateX(0); */
    opacity: 1;
  } 75% {
    /* transform: translateX(10em); */
    opacity: 0;
  } 100% {
    display: none;
  }
}

.Navigation .right .hamburger_menu li{
  font-family: "Red Hat Text",sans-serif;
  font-style: italic;
  font-weight: 700;
  color: #fff;
  text-align: right;
  padding: 0.5em 0;
  text-transform: uppercase;
  border-radius: 20px 0 0 20px;
  cursor: pointer;
  transition: all 100ms ease-in;
}

.Navigation .right .hamburger_menu li:hover{
  background: rgba(204, 204, 204, 0.466);
  padding: 0.5em 1em;
}

.Navigation .right_scrolled .hamburger_menu li{
  color: var(--primary-blue);
}

.Navigation .right_scrolled .hamburger_menu li:hover{
  color: #fff;
  background: var(--primary-blue);
}



